import React from 'react';
import { Typography } from '@material-ui/core';

const wrapWithBold = (props) => {
    return <span style={{fontWeight: "bold"}}>
        {props}
    </span>
}

const HelpPanel = ({ field }) => {
    switch (field) {
        case 'salutation':
            return (
                <>
                    <Typography variant='h6' gutterBottom>
                        Anrede
                    </Typography>
                    <Typography variant='subtitle1' gutterBottom>
                        Bitte whlen Sie die korrekte Anrede fr den ersten Empfnger aus.
                    </Typography>
                </>
            );
        case 'representativeSalutation':
            return (
                <>
                    <Typography variant='h6' gutterBottom>
                        Anrede
                    </Typography>
                    <Typography variant='subtitle1' gutterBottom>
                        Bitte whlen Sie die korrekte Anrede des Bevollmchtigten.
                    </Typography>
                </>
            );
        case 'name':
            return (
                <>
                    <Typography variant='h6' gutterBottom>
                        Vor- und Nachname
                    </Typography>
                    <Typography variant='body1' gutterBottom>
                        Bitte tragen Sie hier {wrapWithBold("Vor- und Nachnamen")} der Person ein, fr welche die Post nachgesendet werden soll. Es knnen bis zu 4 weitere Empfnger aus dem {wrapWithBold("gleichen Haushalt")} eintragen werden.
                    </Typography>
                    <Typography variant='subtitle1' gutterBottom>
                        Hinweise:
                    </Typography>
                    <Typography variant='body1' gutterBottom>
                        Bitte geben Sie {wrapWithBold("keine Eintrge")} wie z.B. {wrapWithBold("Familie Mustermann")} oder {wrapWithBold("Eheleute Mustermann")} ein.
                    </Typography>
                    <Typography variant='body1' gutterBottom>
                        Bitte achten Sie auf die {wrapWithBold("korrekte Schreibweise")} Ihrer persnlichen Daten.
                    </Typography>
                </>
            );
        case 'representativeName':
            return (
                <>
                    <Typography variant='h6' gutterBottom>
                        Vorname und Nachname
                    </Typography>
                    <Typography variant='body1' gutterBottom>
                        Bitte tragen Sie hier den Vor- und Nachnamen des  Bevollmchtigten / Ansprechpartners fr den Auftrag ein.
                    </Typography>
                </>
            );
        case 'email':
            return (
                <>
                    <Typography variant='h6' gutterBottom>
                        E-Mail Adresse
                    </Typography>
                    <Typography variant='body1' gutterBottom>
                        Bitte tragen Sie hier die E-Mail Adresse ein.
                    </Typography>
                    <Typography variant='subtitle1' gutterBottom>
                        Wichtig:
                    </Typography>
                    <Typography variant='body1' gutterBottom>
                        Sie erhalten Ihre Rechnung an die angegebene {wrapWithBold("E-Mail Adresse")}.
                    </Typography>
                    <Typography variant='body1' gutterBottom>
                        Bitte achten Sie auf die {wrapWithBold("korrekte Schreibweise")}.
                    </Typography>
                    <Typography variant='body1' gutterBottom>
                        Die E-Mail Adresse dient dem {wrapWithBold("Erhalt der Rechnung")}.
                    </Typography>
                    <Typography variant='body1' gutterBottom>
                        Die E-Mail Adresse  wird nur bei {wrapWithBold("evtl. Rckfragen zum Auftrag")} durch unser Supportteam und die Postzusteller genutzt.
                    </Typography>
                    <Typography variant='body1' gutterBottom>
                        Sie erhalten von uns {wrapWithBold("keine Werbung")}.
                    </Typography>
                </>
            );
        case 'phoneNumber':
            return (
                <>
                    <Typography variant='h6' gutterBottom>
                        Telefonnummer
                    </Typography>
                    <Typography variant='body1' gutterBottom>
                        Bitte tragen Sie hier die Telefonummer in folgendem Format ein:
                    </Typography>
                    <Typography variant='body1' gutterBottom>
                        {wrapWithBold("Lndervorwahl + Vorwahl + Rufnummer")}
                    </Typography>
                    <Typography variant='body1' gutterBottom>
                        Beispiel:
                    </Typography>
                    <Typography variant='body1' gutterBottom>
                        {wrapWithBold("+49 176 12345678")}
                    </Typography>
                    <Typography variant='body1' gutterBottom>
                        fr mobile Nummern:
                    </Typography>
                    <Typography variant='body1' gutterBottom>
                        {wrapWithBold("+49 176 12345678")}
                    </Typography>
                    <Typography variant='body1' gutterBottom>
                        Die Telefonnummer wird nur bei {wrapWithBold("evtl. Rckfragen zum Auftrag")} durch unser Supportteam und die Postzusteller genutzt.
                    </Typography>
                </>
            );
        case 'representativePhoneNumber':
            return (
                <>
                    <Typography variant='h6' gutterBottom>
                        Telefonnummer
                    </Typography>
                    <Typography variant='body1' gutterBottom>
                        Bitte tragen Sie hier die Telefonummer des Bevollmchtigten in folgendem Format ein:
                    </Typography>
                    <Typography variant='body1' gutterBottom>
                        {wrapWithBold("Lndervorwahl + Vorwahl + Rufnummer")}
                    </Typography>
                    <Typography variant='body1' gutterBottom>
                        Beispiel:
                    </Typography>
                    <Typography variant='body1' gutterBottom>
                        {wrapWithBold("+49 176 12345678")}
                    </Typography>
                    <Typography variant='body1' gutterBottom>
                        fr mobile Nummern:
                    </Typography>
                    <Typography variant='body1' gutterBottom>
                        {wrapWithBold("+49 176 12345678")}
                    </Typography>
                    <Typography variant='body1' gutterBottom>
                        Die Telefonnummer wird nur bei {wrapWithBold("evtl. Rckfragen zum Auftrag")} durch unser Supportteam und die Postzusteller genutzt.
                    </Typography>
                </>
            );
        case 'noticeOfRelocation':
            return (
                <>
                    <Typography variant='h6' gutterBottom>
                        Umzugsmitteilung
                    </Typography>
                    <Typography variant='body1' gutterBottom>
                        Ihre neue Anschrift wird an Ihre geschftlichen Kontakte (z.B. Bank, Versicherung o.. ) mitgeteilt. Diese kennen Ihre alte Anschrift bereits.
                    </Typography>
                    <Typography variant='body1' gutterBottom>
                        Sie erhalten Ihre Post somit an Ihre neue Anschrift und vermeiden Fehladressierungen.
                    </Typography>
                    <Typography variant='body1' gutterBottom>
                        Die Umzugsmitteilung gilt automatisch fr alle im Auftrag genannten Personen.
                    </Typography>
                </>
            );
        case 'parcelShipments':
            return (
                <>
                    <Typography variant='h6' gutterBottom>
                        Pckchen und Pakete
                    </Typography>
                    <Typography variant='subtitle1' gutterBottom>
                        (nur Inland, Zusatzentgelt anhngig von Sendung)
                    </Typography>
                </>
            );
        case 'addressStreetHouse':
            return (
                <>
                    <Typography variant='h6' gutterBottom>
                        Strae und Hausnummer
                    </Typography>
                    <Typography variant='body1' gutterBottom>
                        Bitte tragen Sie hier die Strae und Hausnummer der {wrapWithBold("alten Adresse")} ein.
                    </Typography>
                    <Typography variant='subtitle1' gutterBottom>
                        Beispiel:
                    </Typography>
                    <Typography variant='body1' gutterBottom>
                        Strae: Mustermannstrae
                    </Typography>
                    <Typography variant='body1' gutterBottom>
                        Hausnummer: 1
                    </Typography>
                    <Typography variant='body1' gutterBottom>
                        Bitte achten Sie darauf, die Angaben in die jeweils dafr vorgesehenen Felder eintragen.
                    </Typography>
                </>
            );
        case 'addressZipCodeCity':
            return (
                <>
                    <Typography variant='h6' gutterBottom>
                        Postleitzahl und Ort
                    </Typography>
                    <Typography variant='body1' gutterBottom>
                        Bitte tragen Sie hier die Postleitzahl der {wrapWithBold("alten Adresse")} ein.
                    </Typography>
                    <Typography variant='subtitle1' gutterBottom>
                        Beispiel:
                    </Typography>
                    <Typography variant='body1' gutterBottom>
                        Postleitzahl: "10117"
                    </Typography>
                    <Typography variant='body1' gutterBottom>
                        Ort: "Berlin"
                    </Typography>
                    <Typography variant='body1' gutterBottom>
                        Bitte achten Sie darauf, die Angaben in die jeweils dafr vorgesehenen Felder eintragen.
                    </Typography>
                </>
            );
            case 'newAddressStreetHouse':
            return (
                <>
                    <Typography variant='h6' gutterBottom>
                        Strae und Hausnummer
                    </Typography>
                    <Typography variant='body1' gutterBottom>
                        Bitte tragen Sie hier die Strae und Hausnummer der {wrapWithBold("neuen  Adresse")} ein.
                    </Typography>
                    <Typography variant='subtitle1' gutterBottom>
                        Beispiel:
                    </Typography>
                    <Typography variant='body1' gutterBottom>
                        Strae: Musterstrae
                    </Typography>
                    <Typography variant='body1' gutterBottom>
                        Hausnummer: 1
                    </Typography>
                    <Typography variant='body1' gutterBottom>
                        Bitte achten Sie darauf, die Angaben in die jeweils dafr vorgesehenen Felder eintragen.
                    </Typography>
                </>
            );
        case 'newAddressZipCodeCity':
            return (
                <>
                    <Typography variant='h6' gutterBottom>
                        Postleitzahl und Ort
                    </Typography>
                    <Typography variant='body1' gutterBottom>
                        Bitte tragen Sie hier die Postleitzahl der {wrapWithBold("neuen  Adresse")} ein.
                    </Typography>
                    <Typography variant='subtitle1' gutterBottom>
                        Beispiel:
                    </Typography>
                    <Typography variant='body1' gutterBottom>
                        Postleitzahl: "80639"
                    </Typography>
                    <Typography variant='body1' gutterBottom>
                        Ort: "Mnchen"
                    </Typography>
                    <Typography variant='body1' gutterBottom>
                        Bitte achten Sie darauf, die Angaben in die jeweils dafr vorgesehenen Felder eintragen.
                    </Typography>
                </>
            );
        case 'Land':
            return (
                <>
                    <Typography variant='h6' gutterBottom>
                        Land
                    </Typography>
                    <Typography variant='subtitle1' gutterBottom>
                        Bitte whlen Sie ein Land aus.
                    </Typography>
                </>
            );
        case 'addressSupplement':
            return (
                <>
                    <Typography variant='h6' gutterBottom>
                        Postfach-Nummer
                    </Typography>
                    <Typography variant='subtitle1' gutterBottom>
                        Ein typischer Adresszusatz wre z.B. "c/o Meyer"
                    </Typography>
                </>
            );
        case 'addressPoBox':
            return (
                <>
                    <Typography variant='h6' gutterBottom>
                        Postfach-Nummer
                    </Typography>
                    <Typography variant='subtitle1' gutterBottom>
                        Bitte tragen hier das Postfach der {wrapWithBold("alten Adresse")} ein, von welchem die Post nachgesendet werden soll.
                    </Typography>
                </>
            );
        case 'newAddressPoBox':
            return (
                <>
                    <Typography variant='h6' gutterBottom>
                        Postfach-Nummer
                    </Typography>
                    <Typography variant='subtitle1' gutterBottom>
                        Bitte tragen hier das Postfach der {wrapWithBold("neuen Adresse")} ein, von welchem die Post nachgesendet werden soll.
                    </Typography>
                </>
            );
        case 'anotherPerson':
            return (
                <>
                    <Typography variant='h6' gutterBottom>
                        Weitere Empfnger
                    </Typography>
                    <Typography variant='body1' gutterBottom>
                        Bitte tragen Sie hier den {wrapWithBold("Vor- und Nachnamen")} eines {wrapWithBold("weiteren Empfngers")} ein.
                    </Typography>
                    <Typography variant='subtitle1' gutterBottom>
                        Hinweise:
                    </Typography>
                    <Typography variant='body1' gutterBottom>
                        Der {wrapWithBold("Nachname der weiteren Empfnger")} kann von dem {wrapWithBold("Nachnamen des ersten Empfngers abweichen")}.
                    </Typography>
                    <Typography variant='body1' gutterBottom>
                        {wrapWithBold("Beide Personen")} mssen die {wrapWithBold("gleiche Anschrift")} besitzen.
                    </Typography>
                    <Typography variant='body1' gutterBottom>
                        Sie knnen {wrapWithBold("bis zu 5 Empfnger")} hinzufgen.
                    </Typography>
                </>
            );
        case 'company':
            return (
                <>
                    <Typography variant='h5' gutterBottom>
                        Firmenname
                    </Typography>
                    <Typography variant='body1' gutterBottom>
                        Tragen Sie hier den Namen des Gewerbes oder Selbstndigen ein, fr welchen die Post nachgesendet werden soll.
                    </Typography>
                    <Typography variant='subtitle1' gutterBottom>
                        Beispiel:
                    </Typography>
                    <Typography variant='h6' gutterBottom>
                        Max Mustermann e.K.
                    </Typography>
                    <Typography variant='subtitle1' gutterBottom>
                        oder
                    </Typography>
                    <Typography variant='subtitle1' gutterBottom>
                        Erika Mustermann GmbH
                    </Typography>
                    <Typography variant='body1' gutterBottom>
                        Hinweise:
                    </Typography>
                    <Typography variant='subtitle1' gutterBottom>
                        Bitte tragen Sie den {wrapWithBold("exakten Namen der Firma")} ein und bernehmen Sie dabei die genaue Bezeichnung der {wrapWithBold("Gewerbeanmeldung")} oder {wrapWithBold("Handelsregister-Eintragung")}.
                    </Typography>
                </>
            );
        case 'paymentType':
            return (
                <>
                    <Typography variant='h6' gutterBottom>
                        Nachsendedauer
                    </Typography>
                    <Typography variant='subtitle1' gutterBottom>
                        Bitte whlen Sie die Dauer Ihres Nachsendeauftrags aus.
                    </Typography>
                    <Typography variant='subtitle1' gutterBottom>
                        Whlen Sie zwischen {wrapWithBold("6 Monaten")} und {wrapWithBold("12 Monaten")} aus.
                    </Typography>
                </>
            );
        case 'executionInfo':
            return (
                <>
                    <Typography variant='h6' gutterBottom>
                        Ausfhrungs-Info
                    </Typography>
                    <Typography variant='subtitle1' gutterBottom>
                        Damit wir Ihren Auftrag umgehend bearbeiten knnen und auch bei kurzfristigem Beginn der Nachsendung eine Einrichtung sicherstellen knnen haben Sie hier die Option das Widerrufsrecht zu verkrzen.
                    </Typography>
                    <Typography variant='subtitle1' gutterBottom>
                        Mit der Verkrzung des Widerrufsrecht ermglichen Sie uns sofort mit der Beantragung des Nachsendeauftrag zu beginnen.
                    </Typography>
                    <Typography variant='subtitle1' gutterBottom>
                        Sollte Sie keine schnelle Beantragung bentigen knnen Sie die Checkbox einfach deaktiviert lassen. In diesem Fall haben Sie ein regulres
                    </Typography>
                    <Typography variant='subtitle1' gutterBottom>
                        Widerrufsrecht von 14 Tagen. Eine Bearbeitung Ihres Auftrags findet jedoch in diesem Fall erst nach Ablauf der 14 Tage statt.
                    </Typography>
                </>
            );
        case 'agreeTerms':
            return (
                <>
                    <Typography variant='h5' gutterBottom>
                        Unsere AGB
                    </Typography>
                    <Typography variant='subtitle1' gutterBottom>
                        Bitte akzeptieren Sie unsere AGB um die Bestellung abzuschliessen. Die AGB knnen Sie unter AGB einsehen.
                    </Typography>
                    <Typography variant='h6' gutterBottom>
                        Informationen zum Widerrufsrecht
                    </Typography>
                    <Typography variant='subtitle1' gutterBottom>
                        Sie haben als Verbraucher ein 14-tgiges Widerrufsrecht. Damit Ihr Auftrag zum gewnschten Zeitpunkt starten kann die Dienstleistung auf Ihren Wunsch bereits vor Ablauf der 14 Tage beginnen. Ihnen ist bekannt, dass im Falle eines Widerrufs Wertersatz fr die bereits erbrachten Dienstleistungen geleistet werden muss.
                    </Typography>
                </>
            );
        case 'paymentService':
            return (
                <>
                    <Typography variant='h5' gutterBottom>
                        Zahlungsart auswhlen
                    </Typography>
                    <Typography variant='subtitle1' gutterBottom>
                        Bitte whlen Sie Ihre gewnschte Zahlungsart aus.
                    </Typography>
                </>
            );
        default:
            break;
    }
    return (
        <>
        </>
    );
};

export default HelpPanel;