import React, {useState} from 'react';
import {Button, Tab, Tabs} from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import PaypalImage from '../assets/images/payap1.png';
import StripeImage from '../assets/images/stripe.png';
import VisaSvg from '../assets/images/visa.svg';
import SofortSvg from '../assets/images/sofort.svg';
import KlarnaSvg from '../assets/images/klarna.svg';
import GiropaySvg from '../assets/images/giropay.svg';
import {COLOR_GRAY_1, COLOR_PRIMARY_DARK} from "../constants/color.constant";
import {PAYMENT_SERVICE_PAYPAL, PAYMENT_SERVICE_STRIPE} from "../constants/key.constant";

const useStyles = makeStyles((theme) => ({
    root: {
        flexGrow: 1,
        width: '100%',
        marginTop: 8,
        marginBottom: 8,
        backgroundColor: theme.palette.background.paper,
    },
    logoButton: {
        width: '30%',
        height: '48px',
        [theme.breakpoints.down('xs')]: {
          width: '50%'
        },
    },
    logoPaypal: {
        width: '100%',
        height: '100%',
        maxWidth: '80%',
        maxHeight: '48px'
    },
    logoVisa: {
        width: '100%',
        height: '100%',
        maxWidth: '60%',
        maxHeight: '48px'
    },
    logoSofort: {
        width: '100%',
        height: '100%',
        maxWidth: '60%',
        maxHeight: '48px'
    },
    logoKlarna: {
        width: '100%',
        height: '100%',
        maxWidth: '60%',
        maxHeight: '48px'
    },
    logoGiropay: {
        width: '100%',
        height: '100%',
        maxWidth: '60%',
        maxHeight: '48px'
    },
    logoStripe: {
        width: '100%',
        height: '100%',
        maxWidth: '60%',
        maxHeight: '48px'
    }
}));

const PaymentGateway = ({ paymentService, setPaymentService, setFocused }) => {
    const classes = useStyles();
    const [method, setMethod] = useState(0);
    const handleChange = (e, newMethod) => {
        console.log(newMethod);
        setMethod(newMethod);
        if (newMethod == 0) {
            setPaymentService(PAYMENT_SERVICE_PAYPAL);
        } else {
            setPaymentService(PAYMENT_SERVICE_STRIPE);
        }
        setFocused('paymentService');
    };

    return (
        <div className={classes.root}>
            {/* <Button className={method === PAYMENT_SERVICE_PAYPAL ? classes.logoButtonSelected : classes.logoButton} onClick={e => handleChange(e, PAYMENT_SERVICE_PAYPAL)}>
                <img src={PaypalImage} className={classes.logoPaypal} alt='paypal' />
            </Button>
            <Button className={method === PAYMENT_SERVICE_STRIPE ? classes.logoButtonSelected : classes.logoButton} onClick={e => handleChange(e, PAYMENT_SERVICE_STRIPE)}>
                <img src={StripeImage} className={classes.logoStripe} alt='stripe' />
            </Button>
            <Button className={method === PAYMENT_SERVICE_STRIPE ? classes.logoButtonSelected : classes.logoButton} onClick={e => handleChange(e, PAYMENT_SERVICE_STRIPE)}>
                <img src={StripeImage} className={classes.logoStripe} alt='stripe' />
            </Button>
            <Button className={method === PAYMENT_SERVICE_STRIPE ? classes.logoButtonSelected : classes.logoButton} onClick={e => handleChange(e, PAYMENT_SERVICE_STRIPE)}>
                <img src={StripeImage} className={classes.logoStripe} alt='stripe' />
            </Button>
            <Button className={method === PAYMENT_SERVICE_STRIPE ? classes.logoButtonSelected : classes.logoButton} onClick={e => handleChange(e, PAYMENT_SERVICE_STRIPE)}>
                <img src={StripeImage} className={classes.logoStripe} alt='stripe' />
            </Button>
            <Button className={method === PAYMENT_SERVICE_STRIPE ? classes.logoButtonSelected : classes.logoButton} onClick={e => handleChange(e, PAYMENT_SERVICE_STRIPE)}>
                <img src={StripeImage} className={classes.logoStripe} alt='stripe' />
            </Button> */}
            <Tabs
                value={method}
                onChange={handleChange}
                variant='scrollable'
                scrollButtons='on'
                aria-label='scrollable payment options'
                TabIndicatorProps={{
                    style: {
                        background: COLOR_PRIMARY_DARK
                    }
                }}>
                <Tab id='Paypal'
                    icon={<img src={PaypalImage} className={classes.logoPaypal} alt='paypal' />} 
                    aria-controls='Paypal' className={classes.logoButton} />
                <Tab id='Sofort'
                    icon={<img src={SofortSvg} className={classes.logoSofort} alt='sofort' />} 
                    aria-controls='Sofort' className={classes.logoButton} />
                <Tab id='Giropay'
                    icon={<img src={GiropaySvg} className={classes.logoGiropay} alt='giropay' />} 
                    aria-controls='Giropay' className={classes.logoButton} />
                <Tab id='Klarna'
                    icon={<img src={KlarnaSvg} className={classes.logoKlarna} alt='klarna' />} 
                    aria-controls='Klarna' className={classes.logoButton} />
                <Tab id='Visa'
                    icon={<img src={VisaSvg} className={classes.logoVisa} alt='visa' />} 
                    aria-controls='Visa' className={classes.logoButton} />
            </Tabs>
        </div>
    );
};

export default PaymentGateway;