import React from 'react';
import { Link, makeStyles, Step, StepLabel, Stepper, useMediaQuery } from '@material-ui/core';
import { COLOR_PRIMARY_DARK } from '../constants/color.constant';
import { Link as RouterLink } from 'react-router-dom';

const useStyles = makeStyles((theme) => ({
    root: {
        width: '100%',
        [theme.breakpoints.down('xs')]: {
          display: 'flex',
          justifyContent: 'center'
        },
    },
    stepper: {
        backgroundColor: theme.palette.background.default,
        padding: 0,
        '& .MuiStepIcon-completed, .MuiStepIcon-active': {
          color: COLOR_PRIMARY_DARK
        },
    }
}));

const OrderSteps = ({ step }) => {
    const classes = useStyles();
    const steps = ['Art & Startzeitpunkt', 'Daten Empfnger', 'Anschriften', 'bersicht', 'Zusammenfassung'];
    // const steps = ['B', 'F', 'D', 'W', 'Q'];
    const links = ['/', '/receiver', '/address', '/summary', '/payment'];
    const isXsmall = useMediaQuery(theme =>
        theme.breakpoints.down('sm')
    );

    return (
        <div className={classes.root}>
            <Stepper activeStep={step} className={classes.stepper} orientation={isXsmall ? 'vertical' : 'horizontal'} alternativeLabel={!isXsmall}>
                {steps.map((label, key) => (
                    <Step key={label}>
                        <StepLabel>
                            { key < step ? (
                                <Link component={RouterLink} to={links[key]} color='textPrimary'>
                                    {label}
                                </Link>
                            ) : (
                                <>
                                {label}
                                </>
                            ) }
                        </StepLabel>
                    </Step>
                ))}
            </Stepper>
        </div>
    );
};

export default OrderSteps;